import React, { Component } from "react";
import { Form } from "react-bootstrap";
import { Link, Redirect } from "react-router-dom";
import instance from "../authaxios";
import Error from "./errmessage";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      modal2: false,
      username: null,
      password: null,
      loading: false,
      error: false,
      errmsg: null,
      isLoggedIn: false,
      formErrors: {
        username: "",
        password: "",
      },
    };
  }
  handleSubmit(e) {
    e.preventDefault();
  }
  handleChange = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = this.state.formErrors;

    switch (name) {
      case "username":
        formErrors.username =
          value.length < 4 && value.length > 0
            ? "Numbers only Minimum of 11"
            : "";
        break;

      case "password":
        formErrors.password =
          value.length < 4 && value.length > 0
            ? "Minimum 8 characters Required"
            : "";
        break;

      default:
        break;
    }

    this.setState({ formErrors, [name]: value });
  };
  async Login() {
    this.setState({ loading: true });
    if (
      this.state.formErrors.username === "" &&
      this.state.formErrors.password === "" &&
      this.state.username !== null &&
      this.state.password !== null
    ) {
      try {
        let res = await instance.post("/logincustomer", {
          username: this.state.username,
          password: this.state.password,
          store_id: localStorage.getItem("owner_id"),
        });

        let result = await res.data;

        if (result && result.success) {
          this.setState({ loading: false });
          localStorage.setItem("isLogged", "true");
          localStorage.setItem("customer_id", result.customer_id);
          localStorage.setItem("username", result.username);
          localStorage.setItem("c_name", result.c_name);
          localStorage.setItem("c_phone", result.c_phone);
          localStorage.setItem("c_address", result.c_address);
          this.setState({ isLoggedIn: true });
          return <Redirect to="/" />;
        } else if (result && result.success === false) {
          this.setState({ loading: false });
          this.setState({ errmsg: result.msg });
          this.setState({ error: true });
        }
      } catch (e) {
        console.log(e);
      }
    } else {
      this.setState({ loading: false });
      this.setState({ errmsg: "Please Fill out your form Correctly" });
      this.setState({ error: true });
    }
  }

  render() {
    const { loading, error, errmsg, isLoggedIn } = this.state;
    if (isLoggedIn) {
      return <Redirect to="/" />;
    } else {
      return (
        <>
          <div className="template-v1">
            <div className="home">
              <div className="wrap">
                <Link to="/">
                  <div className="slowmode">
                    <center>
                      <div className="imagetopper">
                        <img
                          src={"" + localStorage.getItem("logo")}
                          alt="logo"
                        />
                      </div>
                    </center>
                  </div>
                </Link>

                <div className="other_inf">
                  <center></center>
                </div>
              </div>
              <div className="wrap">
                <div className="loginsystem">
                  {error && <Error msg={errmsg} />}
                  <h4>Login</h4>
                  <Form onSubmit={this.handleSubmit}>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Email</Form.Label>
                      <Form.Control
                        type="email"
                        placeholder="Email Address"
                        name="username"
                        required
                        onChange={this.handleChange}
                      />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Password</Form.Label>
                      <Form.Control
                        type="password"
                        placeholder="Password"
                        name="password"
                        required
                        onChange={this.handleChange}
                      />
                    </Form.Group>
                    <button onClick={() => this.Login()} className="carts">
                      {loading && (
                        <FontAwesomeIcon
                          style={{ position: "relative", top: "-5px" }}
                          icon={faSpinner}
                          spin
                        />
                      )}{" "}
                      {!loading && <>Log in</>}
                    </button>
                  </Form>
                  <br></br>
                  <Link className="newuser" to="/signup">
                    New user? Sign up
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </>
      );
    }
  }
}

export default Login;
