import React, { Component } from "react";

import instance from "../authaxios";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShoppingBag } from "@fortawesome/free-solid-svg-icons";
import Modals from "./modals";
import Carts from "./carts";

import {
  faInstagram,
  faTwitter,
  faWhatsapp,
  faFacebook,
} from "@fortawesome/free-brands-svg-icons";

class Home extends Component {
  typingTimer = null;

  handleChanger = (evt) => {
    const val = evt.target.value;
    clearTimeout(this.typingTimer);
    this.typingTimer = setTimeout(() => {
      if (val) {
        this.search(val);
      }
    }, 1000);
  };
  componentWillUnmount() {
    clearTimeout(this.typingTimer);
  }
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      modals: false,
      modal2: false,
      dataavail: false,
      store_name: "",
      store_phone: "",
      store_address: "",
      store_logo: "",
      p_id: "",
      data: true,
      p_title: "",
      p_description: "",
      p_price: "",
      resultts: [],
      p_cprice: "",
      numrow: "",
      results: [],
      load_id: null,
      searcher: false,
      loading: false,
      image_link: "",
      val: "",
      facebook: "",
      twitter: "",
      instagram: "",
      email: "",
      whatsapp: "",
      store_description: "",
      brief_description: "",
    };
  }
  selectModal = (info) => {
    this.setState({ modal: false, modal2: false, modals: false }); // true/false toggle
    this.refresh();
  };
  async displayProduct(title, description, id, price, image) {
    this.setState({
      p_id: id,
      p_title: title,
      p_description: description,
      p_price: price,
      image_link: image,
      modal: true,
    });
  }
  async viewAbout() {
    this.setState({ modals: true });
  }
  async cartProduct() {
    this.setState({ modal2: true });
  }
  async Logout() {
    localStorage.removeItem("isLogged");
    localStorage.removeItem("customer_id");
    localStorage.removeItem("username");
    localStorage.removeItem("c_name");
    localStorage.removeItem("c_phone");
    localStorage.removeItem("c_address");
  }
  async search(val) {
    this.setState({ val: val });
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/fetchproductsearch", {
        store_id: store_id,
        val: val,
      });

      const resultts = await res.data;
      this.setState({ resultts });
      this.setState({ searcher: true });
      this.refresh();
    } catch (e) {
      console.log(e);
    }
  }

  async componentDidMount() {
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/fetchproducts", {
        store_id: store_id,
      });

      const results = await res.data;
      if (results.length > 0) {
        this.setState({ dataavail: true, results, data: false });
      } else {
        this.setState({ dataavail: false, results, data: false });
      }

      this.refresh();
    } catch (e) {
      console.log(e);
    }

    try {
      let res = await instance.post("/ownerprofile", {
        store_id: store_id,
      });

      let result = await res.data;

      if (result && result.success) {
        this.refresh();

        this.setState({
          store_name: result.store_name,
          store_phone: result.store_phone,
          store_address: result.store_address,
          store_logo: result.store_logo,
          brief_description: result.brief_description,
          store_description: result.store_description,
          whatsapp: result.whatsapp,
          facebook: result.facebook,
          twitter: result.twitter,
          instagram: result.instagram,
          email: result.email,
        });
      }
    } catch (e) {
      console.log(e);
    }

    try {
      let res = await instance.post("/viewcart", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: store_id,
      });

      const results = await res.data;

      this.setState({ numrow: results.length });
    } catch (e) {
      console.log(e);
    }
  }
  async refresh() {
    try {
      let res = await instance.post("/viewcart", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: localStorage.getItem("owner_id"),
      });

      const results = await res.data;

      this.setState({ numrow: results.length });
    } catch (e) {
      console.log(e);
    }
  }

  async addCart(p_id, p_title, image_link, amount) {
    this.setState({ load_id: p_id });
    let store_id = localStorage.getItem("owner_id");
    let customer_info = null;
    let unit = 1;
    try {
      let res = await instance.post("/insertcart", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: store_id,
        p_id: p_id,
        customer_info: customer_info,
        p_title: p_title,
        image_link: image_link,
        unit: unit,
        amount: amount,
      });

      let result = await res.data;

      if (result && result.success) {
        this.refresh();
        this.setState({ load_id: null });
        this.setState({ gmsg: result.msg });
        this.setState({ good: true });
        this.setState({ error: false });
        this.setState({ modal2: true });
        this.setState({ modal: false });
      } else if (result && result.success === false) {
        this.setState({ load_id: null });
        this.refresh();
        this.setState({ errmsg: result.msg });
        this.setState({ error: true });
        this.setState({ good: false });
        return alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }

  render() {
    const { modals, modal2 } = this.state;

    return (
      <>
        <div
          className="tempbs"
          style={{ background: localStorage.getItem("bg") }}
        >
          <div className="wrapers">
            <div class="headers">
              <div className="lleft">Swip</div>
              <div className="btw">
                <span>{localStorage.getItem("subdomain")} </span>
              </div>

              <div className="lright">
                <FontAwesomeIcon
                  className="fabag"
                  icon={faShoppingBag}
                  onClick={() => this.cartProduct()}
                />
                {this.state.numrow !== 0 && (
                  <div className="circlecount">{this.state.numrow}</div>
                )}

                {modal2 && (
                  <Carts
                    facebook={this.state.facebook}
                    twitter={this.state.twitter}
                    email={this.state.email}
                    whatsapp={this.state.whatsapp}
                    instagram={this.state.instagram}
                    displayModal={this.state.modal2}
                    closeModal={this.selectModal}
                  />
                )}
              </div>
            </div>

            <div class="main-middle-containers">
              <div className="topagents">
                <span>By the {localStorage.getItem("name")}</span>
                <br></br>
                <p>{this.state.brief_description}</p>
              </div>

              <div className="container">
                <div className="columns">
                  <div style={{ width: "100%", color: "#fff !important" }}>
                    <div className="allcart-cart">
                      <div className="abtc-view">
                        <div className="abtc">
                          <Link
                            to="/"
                            style={{
                              color: "#fff",
                              textDecoration: "underline",
                              fontWeight: "700",
                            }}
                          >
                            Back Home
                          </Link>
                          <h6 style={{ color: "#fff" }}>Boom!</h6>

                          <span style={{ color: "#fff" }}>
                            Thank you for buying with us,a shop by Lightweb
                            Technology Limited
                          </span>
                          <br></br>
                          <span style={{ color: "#fff" }}>
                            Your order reference number is:{" "}
                            {this.props.match.params.id}
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <br></br>
                </div>
              </div>
            </div>

            <div class="footers">
              <div className="so">
                {modals && (
                  <Modals
                    description={this.state.store_description}
                    facebook={this.state.facebook}
                    twitter={this.state.twitter}
                    email={this.state.email}
                    whatsapp={this.state.whatsapp}
                    instagram={this.state.instagram}
                    addres={localStorage.getItem("address")}
                    displayModal={this.state.modals}
                    closeModal={this.selectModal}
                  />
                )}
                <span onClick={() => this.viewAbout()}>About Us</span>
              </div>
              <div className="social">
                <span>
                  {" "}
                  <a
                    href={"https://instagram.com/" + this.state.instagram}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <FontAwesomeIcon className="fasocial" icon={faInstagram} />
                  </a>
                </span>
                <span>
                  {" "}
                  <a
                    href={"https://twitter.com/" + this.state.twitter}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {" "}
                    <FontAwesomeIcon className="fasocial" icon={faTwitter} />
                  </a>
                </span>
                <span>
                  {" "}
                  <a
                    href={
                      "https://api.whatsapp.com/send?phone=+234" +
                      this.state.whatsapp +
                      "&amp;text=Hello, I have a question for " +
                      localStorage.getItem("subdomain") +
                      " https://" +
                      localStorage.getItem("subdomain") +
                      ".swip.ng"
                    }
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <FontAwesomeIcon className="fasocial" icon={faWhatsapp} />
                  </a>
                </span>
                <span>
                  {" "}
                  <a
                    href={"https://facebook.com/" + this.state.facebook}
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    {" "}
                    <FontAwesomeIcon className="fasocial" icon={faFacebook} />
                  </a>
                </span>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Home;
