import React, { Component } from "react";
import instance from "../authaxios";
import { Form, Row, Col } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrash,
  faShoppingBag,
  faArrowLeft,
} from "@fortawesome/free-solid-svg-icons";
import {
  faInstagram,
  faTwitter,
  faWhatsapp,
  faFacebook,
} from "@fortawesome/free-brands-svg-icons";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { FlutterWaveButton, closePaymentModal } from "flutterwave-react-v3";

class CartData extends Component {
  constructor(props) {
    super(props);
    this.state = {
      view: 1,
      sumcart: null,
      sumcarts: null,
      r_no: "",
      results: [],
      loading: false,
      delivery: "",
      discount: "",
      name: "",
      phone: "",
      address: "",
      city: "",
      state: "",
    };
  }

  selectModal = (info) => {
    this.setState({ modal: false, modal2: false }); // true/false toggle
  };
  async displayProduct(id, title, description, price, cprice) {
    this.setState({
      load_id: null,
      p_id: id,
      p_title: title,
      p_description: description,
      p_price: price,
      p_cprice: cprice,
      modal: true,
    });
  }
  async cartProduct() {
    this.setState({ modal2: true });
  }
  async componentDidMount() {
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/viewcart", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: store_id,
      });

      const results = await res.data;
      this.setState({ results });
    } catch (e) {
      console.log(e);
    }
    try {
      let res = await instance.post("/carttotal", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: store_id,
      });

      let result = await res.data;
      this.setState({ sumcarts: result.sumcart });
    } catch (e) {
      console.log(e);
    }
  }
  async next() {
    let r_no =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZ√ç".charAt(Math.floor(9 + Math.random() * 9)) +
      Math.floor(100000 + Math.random() * 900000);
    this.setState({
      r_no: r_no,
    });
    this.setState({ loading: true });

    try {
      let res = await instance.post("/appointcart", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: localStorage.getItem("owner_id"),
        r_no: r_no,
      });

      let result = await res.data;
      if (result && result.success) {
        this.setState({ loading: false, view: 2 });
        /*     let store_id = localStorage.getItem("owner_id");
         try {
            let res = await instance.post("/carttotal", {
              customer_id: localStorage.getItem("customer_id"),
              store_id: store_id,
            });

            let result = await res.data;
            if (result && result.success && this.state.delivery === "pickup") {
              this.refresh();

              this.setState({ sumcart: result.sumcart });
            } else if (
              result &&
              result.success &&
              this.state.delivery === "delivery"
            ) {
              this.refresh();
              let sum1 = result.sumcart;
              let sum2 = localStorage.getItem("delivery_fee");
              let sumsum = parseInt(sum1) + parseInt(sum2);
              this.setState({
                sumcart: sumsum,
              });
            } else if (result && result.success === false) {
              this.refresh();
            }
          } catch (e) {
            console.log(e);
          }
          */
      } else {
        this.setState({ loading: false });
        return alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  async nexts() {
    if (
      this.state.delivery !== "" &&
      this.state.name !== "" &&
      this.state.phone !== ""
    ) {
      this.setState({ loading: true });

      try {
        let res = await instance.post("/appointcustomer", {
          customer_id: localStorage.getItem("customer_id"),
          store_id: localStorage.getItem("owner_id"),
          customer_info:
            this.state.name +
            "-" +
            this.state.phone +
            "-" +
            this.state.address +
            "-" +
            this.state.city +
            "-" +
            this.state.state,
        });

        let result = await res.data;
        if (result && result.success) {
          if (this.state.delivery === "pickup") {
            this.refresh();
            this.refreshes();

            this.setState({ sumcart: this.state.sumcarts });
            this.setState({ loading: false, view: 3 });
          } else if (this.state.delivery === "delivery") {
            let sum1 = this.state.sumcarts;
            let sum2 = localStorage.getItem("delivery_fee");
            let sumsum = parseInt(sum1) + parseInt(sum2);
            this.setState({
              sumcart: sumsum,
            });

            this.refresh();
            this.refreshes();
            this.setState({ loading: false, view: 3 });
          }
        } else {
          this.setState({ loading: false });
          return alert(result.msg);
        }
      } catch (e) {
        console.log(e);
      }
    }
  }
  async prev() {
    this.setState({ view: this.state.view - 1 });
  }
  async success(status) {
    if (status === "successful") {
      let store_id = localStorage.getItem("owner_id");
      try {
        let res = await instance.post("/success", {
          store_id: store_id,
          r_no: this.state.r_no,
        });

        const result = await res.data;
        if (result && result.success) {
          this.refresh();
        }
      } catch (e) {
        console.log(e);
      }
    }
  }
  async refreshes() {
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/carttotal", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: store_id,
      });

      let result = await res.data;
      this.setState({ sumcarts: result.sumcart });
    } catch (e) {
      console.log(e);
    }
  }
  async refresh() {
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/viewcart", {
        customer_id: localStorage.getItem("customer_id"),
        store_id: store_id,
      });

      const results = await res.data;
      this.setState({ results });
    } catch (e) {
      console.log(e);
    }
  }
  async deletecart(p_id) {
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/deletecart", {
        customer_id: localStorage.getItem("customer_id"),
        p_id: p_id,
        store_id: store_id,
      });

      let result = await res.data;
      if (result && result.success) {
        this.refresh();
        this.refreshes();
      } else if (result && result.success === false) {
        this.refresh();
        this.refreshes();
        return alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  async pluscart(p_id, amount) {
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/pluscart", {
        customer_id: localStorage.getItem("customer_id"),
        p_id: p_id,
        store_id: store_id,
        amount: amount,
      });

      let result = await res.data;
      if (result && result.success) {
        this.refresh();
        this.refreshes();
      } else if (result && result.success === false) {
        this.refresh();
        this.refreshes();
        return alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  async minuscart(p_id, amount) {
    let store_id = localStorage.getItem("owner_id");
    try {
      let res = await instance.post("/minuscart", {
        customer_id: localStorage.getItem("customer_id"),
        p_id: p_id,
        store_id: store_id,
        amount: amount,
      });

      let result = await res.data;
      if (result && result.success) {
        this.refresh();
        this.refreshes();
      } else if (result && result.success === false) {
        this.refresh();
        this.refreshes();
        return alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  handleChange = (e) => {
    e.preventDefault();
    const { name, value } = e.target;

    this.setState({ [name]: value });
  };
  handleSubmit(e) {
    e.preventDefault();
  }
  async payys() {
    try {
      let res = await instance.post("/success", {
        store_id: localStorage.getItem("owner_id"),
        amount: this.state.sumcart,
        r_no: this.state.r_no,
        delivery: this.state.delivery,
        delivery_fee: 0 + parseInt(localStorage.getItem("delivery_fee")),
      });
      let rest = await res.data;

      if (rest && rest.success === true) {
        this.setState({ view: 4 });
      }
      if (rest && rest.success === false) {
        return alert(rest.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  async redirect() {
    return (window.location = "/success/" + this.state.r_no);
  }
  render() {
    const { view, loading } = this.state;
    const config = {
      public_key: "FLWPUBK-7ca020fc14447f7b59861061438060ae-X",
      tx_ref: this.state.r_no,
      amount: parseInt(this.state.sumcart + 0.03 * this.state.sumcart),
      currency: "NGN",
      payment_options: "card,mobilemoney,ussd",
      customer: {
        email: "hello@swip.ng",
        phonenumber: localStorage.getItem("c_phone"),
        name: localStorage.getItem("c_name"),
      },
      customizations: {
        title: localStorage.getItem("name"),
        description: "Payment for items in cart",
        logo: localStorage.getItem("logo"),
      },
    };
    const fwConfig = {
      ...config,
      text:
        "Pay NGN " + parseInt(this.state.sumcart + 0.03 * this.state.sumcart),
      callback: (response) => {
        console.log(response);
        if (response.status === "successful") {
          this.payys();
          closePaymentModal();
          this.redirect();
        }
        // this will close the modal programmatically
      },
      onClose: () => {},
    };
    if (view === 1) {
      return (
        <>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              borderBottom: "1px solid #f5f5f5",
              paddingBottom: "15px",
              paddingTop: "15px",
            }}
          >
            <div>
              <FontAwesomeIcon
                className="ct"
                style={{ fontSize: "25px", color: localStorage.getItem("bg") }}
                icon={faShoppingBag}
                onClick={() => this.cartProduct()}
              />
            </div>
            <div>
              <p>
                NGN {this.state.sumcarts === null && 0}
                {this.state.sumcarts !== null && (
                  <>
                    {parseInt(this.state.sumcarts)
                      .toString()
                      .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                  </>
                )}
              </p>
            </div>
          </div>

          <div className="allcart-cart">
            {this.state.results.map((result) => (
              <div className="cart-item">
                <div>
                  {" "}
                  <FontAwesomeIcon
                    className="trash"
                    onClick={() => this.deletecart(result.p_id)}
                    icon={faTrash}
                  />
                </div>
                <div>
                  <img src={"" + result.image_link} alt="images" />
                </div>
                <div>
                  <h4> {result.p_title}</h4>
                </div>
                <div className="money-cart">
                  <p>NGN {result.total} </p>
                </div>

                <div className="monetary">
                  <button
                    disabled={result.unit === 1 && true}
                    className="add"
                    onClick={() => this.minuscart(result.p_id, result.amount)}
                  >
                    -
                  </button>

                  {result.unit}
                  <button
                    className="add"
                    onClick={() => this.pluscart(result.p_id, result.amount)}
                  >
                    +
                  </button>
                </div>
              </div>
            ))}
            <p></p>
          </div>

          <div className="checkout-cart">
            <button
              className="carts"
              style={{ background: localStorage.getItem("bg") }}
              onClick={() => this.next()}
            >
              {loading && <FontAwesomeIcon icon={faSpinner} spin />}{" "}
              {!loading && <> Check Out</>}
            </button>
          </div>
        </>
      );
    } else if (view === 2) {
      return (
        <>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              borderBottom: "1px solid #f5f5f5",
              paddingBottom: "15px",
              paddingTop: "15px",
            }}
          >
            <div>
              <span className="shiphead">
                {" "}
                <FontAwesomeIcon
                  onClick={() => this.prev()}
                  style={{
                    fontSize: "20px",
                    marginRight: "20px",
                    color: "#000000",
                    cursor: "pointer",
                  }}
                  icon={faArrowLeft}
                />
                {localStorage.getItem("subdomain")}
              </span>
              <br></br>
              <span className="shipabt abts">Shipping</span>
              <span className="shipabt"> {">"} </span>
              <span className="shipabt">Summary</span>
            </div>
          </div>

          <div className="allcart-cart">
            <h6>Personal Information</h6>

            <Form>
              <Row>
                <Col md={6}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Control
                      type="text"
                      name="name"
                      value={this.state.name ? this.state.name : ""}
                      onChange={this.handleChange}
                      placeholder="Name"
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Control
                      type="text"
                      name="phone"
                      value={this.state.phone ? this.state.phone : ""}
                      onChange={this.handleChange}
                      placeholder="Mobile No."
                    />
                  </Form.Group>
                </Col>
              </Row>
              <h6>Delivery Address</h6>
              <Row>
                <Col md={12}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Control
                      type="text"
                      name="address"
                      value={this.state.address ? this.state.address : ""}
                      onChange={this.handleChange}
                      placeholder="Full address"
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Control
                      type="text"
                      name="city"
                      value={this.state.city ? this.state.city : ""}
                      onChange={this.handleChange}
                      placeholder="City e.g Damaturu"
                    />
                  </Form.Group>
                </Col>
                <Col md={6}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Control
                      type="text"
                      name="state"
                      value={this.state.state ? this.state.state : ""}
                      onChange={this.handleChange}
                      placeholder="State e.g Yobe"
                    />
                  </Form.Group>
                </Col>
                <Col md={12}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Control name="country" as="select" required>
                      <option>Nigeria</option>
                    </Form.Control>
                  </Form.Group>
                </Col>
              </Row>
              <h6>Delivery Option</h6>
              <Row>
                <Col md={12}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Control
                      name="delivery"
                      as="select"
                      onChange={this.handleChange}
                      required
                    >
                      <option></option>
                      <option>pickup</option>
                      {localStorage.getItem("delivery") === "yes" && (
                        <option>delivery</option>
                      )}
                    </Form.Control>
                  </Form.Group>
                </Col>
              </Row>
            </Form>
          </div>

          <div className="checkout-cart">
            <button
              className="carts"
              style={{ background: localStorage.getItem("bg") }}
              onClick={() => this.nexts()}
            >
              {loading && <FontAwesomeIcon icon={faSpinner} spin />}{" "}
              {!loading && <> Proceed to pay</>}
            </button>
          </div>
        </>
      );
    } else if (view === 3) {
      return (
        <>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              borderBottom: "1px solid #f5f5f5",
              paddingBottom: "15px",
              paddingTop: "15px",
            }}
          >
            <span className="shiphead">
              {" "}
              <FontAwesomeIcon
                onClick={() => this.prev()}
                style={{
                  fontSize: "20px",
                  marginRight: "20px",
                  color: "#000000",
                  cursor: "pointer",
                }}
                icon={faArrowLeft}
              />
              Order Summary
            </span>
          </div>
          <div className="allcart-cart">
            {this.state.results.map((result, index) => (
              <div className="cart-item1">
                {index + 1}
                <div>
                  <h4>{result.p_title} </h4>
                </div>
                <div className="money-cart">
                  <p>
                    NGN {result.amount} x{result.unit}
                  </p>
                </div>
                <div className="money-cart">
                  <p>Total: NGN {result.total} </p>
                </div>
              </div>
            ))}
            <div className="cabt">
              <h6>Customer Information</h6>
              <p>
                Name: {this.state.name}
                <br></br>
                Phone Number: {this.state.phone}
                <br></br>
              </p>
              <h6>Delivery Address</h6>
              <p>
                {this.state.address},{this.state.city},{this.state.state}
              </p>
            </div>
            <div className="totals">
              <hr />
              <div class="cont">
                <h4 className="left">Sub total</h4>
                <p className="right">NGN {this.state.sumcarts}</p>
              </div>
              <div class="cont">
                <h4 className="left">VAT(3%)</h4>
                <p className="right">NGN {0.03 * this.state.sumcart}</p>
              </div>
              <div class="cont">
                {this.state.delivery === "pickup" && (
                  <>
                    <h4 className="left">Pickup</h4>
                    <p className="right">NGN 0</p>
                  </>
                )}
                {this.state.delivery === "delivery" && (
                  <>
                    <h4 className="left">Delivery fee</h4>
                    <p className="right">
                      NGN {localStorage.getItem("delivery_fee")}
                    </p>
                  </>
                )}
              </div>
              <div class="cont">
                <h4 className="left">Total</h4>
                <p className="right">
                  NGN {this.state.sumcart + 0.03 * this.state.sumcart}
                </p>
              </div>
            </div>
          </div>

          <div className="checkout-cart">
            <div
              className="carts"
              style={{ background: localStorage.getItem("bg") }}
            >
              <FlutterWaveButton {...fwConfig} />
            </div>
          </div>
        </>
      );
    } else if (view > 3) {
      return (
        <>
          <div className="allcart-cart">
            <div className="abtc-view">
              <div className="abtc">
                <h6>Boom!</h6>
                <br></br>
                <span>
                  Thank you for buying with us,a shop by Lightweb Technology
                  Limited
                </span>
                <br></br>
                <div className="social">
                  <span>
                    {" "}
                    <a
                      href={"https://instagram.com/" + this.props.instagram}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        className="fasocialss"
                        style={{ color: "#dd2a7b" }}
                        icon={faInstagram}
                      />
                    </a>
                  </span>
                  <span>
                    {" "}
                    <a
                      href={"https://twitter.com/" + this.props.twitter}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        className="fasocialss"
                        style={{ color: "#4576c2" }}
                        icon={faTwitter}
                      />
                    </a>
                  </span>
                  <span>
                    {" "}
                    <a
                      href={
                        "https://api.whatsapp.com/send?phone=+234" +
                        this.props.whatsapp +
                        "&amp;text=Hello, I have a question for " +
                        localStorage.getItem("subdomain") +
                        " https://" +
                        localStorage.getItem("subdomain") +
                        ".swip.ng"
                      }
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        className="fasocialss"
                        style={{ color: "#25d366" }}
                        icon={faWhatsapp}
                      />
                    </a>
                  </span>
                  <span>
                    {" "}
                    <a
                      href={"https://facebook.com/" + this.props.facebook}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        className="fasocialss"
                        style={{ color: "#1877f2" }}
                        icon={faFacebook}
                      />
                    </a>
                  </span>
                </div>
              </div>
            </div>
            <div className="checkout-cart">
              <div className="abtc">
                <span>We`ve sent an sms to {this.state.phone}</span>
              </div>
            </div>
          </div>
        </>
      );
    }
  }
}

export default CartData;
