import React, { Component } from "react";
import instance from "../authaxios";
import AliceCarousel from "react-alice-carousel";

class Images extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 1,
      results: [],
      p_id: props.p_id,
      avail: false,
    };
  }
  nextStep = () => {
    const { step } = this.state;
    if (this.state.results.length === step) {
      this.setState({
        step: step,
      });
    } else {
      this.setState({
        step: step + 1,
      });
    }
  };
  continue = (e) => {
    e.preventDefault();
    this.nextStep();
  };
  //Go Back
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({
        step: 1,
      });
    } else {
      this.setState({
        step: step - 1,
      });
    }
  };
  back = (e) => {
    e.preventDefault();
    this.prevStep();
  };
  async componentDidMount() {
    try {
      let res = await instance.post("/viewimages", {
        p_id: this.state.p_id,
      });

      const results = await res.data;
      if (results && results.length > 0) {
        this.setState({ avail: true });
      }
      this.setState({ results });
    } catch (e) {
      console.log(e);
    }
  }
  render() {
    const { results, avail } = this.state;
    return (
      <>
        {avail && (
          <AliceCarousel autoPlay autoPlayInterval="3000">
            {results.map((result, index) => (
              <>
                <img
                  src={"" + result.image_link}
                  className="sliderimg"
                  alt=""
                  style={{ borderRadius: "10px" }}
                />
              </>
            ))}
          </AliceCarousel>
        )}

        {!avail && <p>No image to preview</p>}
      </>
    );
  }
}
export default Images;
