import React from "react";

import CartDatas from "./cartdatas";

const Modal = (props) => {
  const divStyle = {
    display: props.displayModal ? "block" : "none",
  };
  function closeModal(e) {
    e.stopPropagation();
    props.closeModal();
  }

  return (
    <div className="modal-cart" onClick={closeModal} style={divStyle}>
      {" "}
      <div className="modal-content-cart" onClick={(e) => e.stopPropagation()}>
        {" "}
        <span className="close-cart" onClick={closeModal}>
          &times;
        </span>{" "}
        <div className="contentent" style={{ margin: "0 auto", width: "90%" }}>
          <CartDatas
            facebook={props.facebook}
            twitter={props.twitter}
            email={props.email}
            whatsapp={props.whatsapp}
            instagram={props.instagram}
            closeModal={closeModal}
          />
        </div>
      </div>{" "}
    </div>
  );
};
export default Modal;
