import React, { Component } from "react";
import { Table } from "react-bootstrap";
import { Grid, Cell } from "react-mdl";
import instance from "../authaxios";
import { Link } from "react-router-dom";

class Orders extends Component {
  constructor(props) {
    super(props);
    this.state = {
      results: [],
    };
  }

  async componentDidMount() {
    try {
      let res = await instance.post("/customerorder", {
        store_id: localStorage.getItem("owner_id"),
        customer_id: localStorage.getItem("customer_id"),
      });

      const results = await res.data;
      this.setState({ results });
    } catch (e) {
      console.log(e);
    }
  }

  render() {
    return (
      <>
        <div className="template-v1">
          <div className="home">
            <div className="wrap">
              <div className="slowmode">
                <center>
                  <div className="imagetopper">
                    <img src={"" + localStorage.getItem("logo")} alt="logo" />
                  </div>
                </center>

                <h2>{localStorage.getItem("name")}</h2>
              </div>

              <div className="other_inf">
                <center>
                  <p>
                    Our Shop is Currently closed so we can only deliver to your
                    homes for now.
                  </p>
                </center>
              </div>
            </div>
            <div className="wrap">
              <Grid>
                <Cell col={8}>
                  <div className="userlog">
                    <Link
                      to="/"
                      className="loger"
                      style={{ position: "relative", top: "-1.2em" }}
                    >
                      Home
                    </Link>
                  </div>
                </Cell>

                <Cell col={4}></Cell>
              </Grid>

              <div className="product_section">
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Refrence No</th>
                      <th>Product Name</th>
                      <th>Unit</th>
                      <th>Amount</th>
                      <th>Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.results.map((result, index) => (
                      <tr>
                        <th scope="row">{index + 1}</th>
                        <td>{result.reference_no}</td>
                        <td>{result.product_name}</td>
                        <td>{result.unit}</td>
                        <td>{result.amount}</td>
                        <td>{result.date}</td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
              </div>
            </div>
            <div
              className="other_inf"
              style={{
                position: "relative",
                marginTop: "0",
                padding: "0",
                top: "3em",
              }}
            >
              <center>
                <p>
                  {localStorage.getItem("address")}
                  <br></br>
                  {localStorage.getItem("phone")}
                </p>
              </center>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Orders;
