import React, { useState } from "react";
import { Grid, Cell } from "react-mdl";

import Images from "./images";
const Modal = (props) => {
  const divStyle = {
    display: props.displayModal ? "block" : "none",
  };
  function closeModal(e) {
    e.stopPropagation();
    props.closeModal();
  }
  const [seller, setSeller] = useState();
  const handleSeller = (event) => {
    if (seller === true) {
      setSeller(false);
    } else {
      setSeller(true);
    }
  };

  return (
    <div className="modal" onClick={closeModal} style={divStyle}>
      {" "}
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        {" "}
        <span className="close" onClick={closeModal}>
          &times;
        </span>{" "}
        <div className="contentent" style={{ margin: "0 auto", width: "100%" }}>
          <Grid>
            <Cell col={6}>
              <Images p_id={props.p_id} />
            </Cell>
            <Cell col={6}>
              <div className="description">
                <h2>{props.title}</h2>
                <p> {props.description}</p>
                <div
                  className="left prices"
                  style={{
                    position: "relative",
                  }}
                >
                  NGN
                  {props.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                </div>

                <button
                  style={{
                    background: localStorage.getItem("bg"),
                    width: "40%",
                  }}
                  onClick={props.onClick}
                  className="carter"
                >
                  Add to cart
                </button>
                <button
                  style={{
                    background: "#ccc",
                    color: "#57584e",
                    width: "40%",
                    marginLeft: "10px",
                  }}
                  onClick={handleSeller}
                  className="carter"
                >
                  Contact seller
                </button>
                {seller && (
                  <div className="cperson">Phone No: {props.whatsapp}</div>
                )}
              </div>
            </Cell>
          </Grid>
        </div>
      </div>{" "}
    </div>
  );
};
export default Modal;
