import React, { Component } from "react";
import { Form } from "react-bootstrap";
import { Link, Redirect } from "react-router-dom";
import instance from "../authaxios";
import Error from "./errmessage";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Signup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      modal2: false,
      name: "",
      email: "",
      password: "",
      phone: "",
      address: "",
      loading: false,
      error: false,
      errmsg: null,
    };
  }
  handleSubmit(e) {
    e.preventDefault();
  }
  handleChange = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = this.state.formErrors;
    this.setState({ formErrors, [name]: value });
  };
  async Create() {
    this.setState({ loading: true });
    if (
      this.state.email !== "" &&
      this.state.password !== "" &&
      this.state.name !== "" &&
      this.state.phone !== "" &&
      this.state.address !== ""
    ) {
      try {
        let res = await instance.post("/createcustomer", {
          username: this.state.email,
          password: this.state.password,
          c_name: this.state.name,
          c_phone: this.state.phone,
          c_address: this.state.address,
          store_id: localStorage.getItem("owner_id"),
        });

        let result = await res.data;

        if (result && result.success) {
          this.setState({ loading: false });
          localStorage.setItem("isLogged", "true");
          localStorage.setItem("customer_id", result.customer_id);
          localStorage.setItem("username", result.username);
          localStorage.setItem("c_name", result.c_name);
          localStorage.setItem("c_phone", result.c_phone);
          localStorage.setItem("c_address", result.c_address);
          this.setState({ isLoggedIn: true });
          return <Redirect to="/" />;
        } else if (result && result.success === false) {
          this.setState({ loading: false });
          this.setState({ errmsg: result.msg });
          this.setState({ error: true });
        }
      } catch (e) {
        console.log(e);
      }
    } else {
      this.setState({ loading: false });
      this.setState({ errmsg: "Please Fill out your form Correctly" });
      this.setState({ error: true });
    }
  }
  render() {
    const { loading, error, errmsg, isLoggedIn } = this.state;
    if (isLoggedIn) {
      return <Redirect to="/" />;
    } else {
      return (
        <>
          <div className="template-v1">
            <div className="home">
              <div className="wrap">
                <Link to="/">
                  <div className="slowmode">
                    <center>
                      <div className="imagetopper">
                        <img
                          src={"" + localStorage.getItem("logo")}
                          alt="logo"
                        />
                      </div>
                    </center>
                  </div>
                </Link>

                <div className="other_inf">
                  <center></center>
                </div>
              </div>
              <div className="wrap">
                <div className="loginsystem">
                  {error && <Error msg={errmsg} />}
                  <h4>Sign up</h4>
                  <Form onSubmit={this.handleSubmit}>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Name</Form.Label>
                      <Form.Control
                        type="text"
                        name="name"
                        onChange={this.handleChange}
                        placeholder="e.g Hussaini Bulama"
                      />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Email</Form.Label>
                      <Form.Control
                        type="email"
                        name="email"
                        onChange={this.handleChange}
                        placeholder="Email Address"
                      />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Password</Form.Label>
                      <Form.Control
                        type="password"
                        name="password"
                        onChange={this.handleChange}
                        placeholder="Password"
                      />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Phone Number</Form.Label>
                      <Form.Control
                        type="text"
                        name="phone"
                        onChange={this.handleChange}
                        placeholder="e.g 08136668344"
                      />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                      <Form.Label>Address</Form.Label>
                      <Form.Control
                        name="address"
                        onChange={this.handleChange}
                        as="textarea"
                        rows="5"
                      />
                    </Form.Group>
                    <button onClick={() => this.Create()} className="carts">
                      {loading && (
                        <FontAwesomeIcon
                          style={{ position: "relative", top: "-5px" }}
                          icon={faSpinner}
                          spin
                        />
                      )}{" "}
                      {!loading && <> Sign up</>}
                    </button>
                  </Form>
                  <br></br>
                  <Link className="newuser" to="/login">
                    Already have an account? Login
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </>
      );
    }
  }
}

export default Signup;
